package net

import api.vk.VkApiException
import kotlinx.browser.window
import org.w3c.fetch.Response
import org.w3c.xhr.XMLHttpRequest
import kotlin.js.Promise

external fun fetch(url: String, options: dynamic): Promise<Response>

@SinceKotlin("1.1")
private inline fun <T> also(t: T, block: (T) -> Unit): T {
    block(t)
    return t
}

object VkNetManager {
    suspend fun make(url: String, paramsBuilder: NetManager.ParamBuilder.() -> Unit): dynamic {
        val resp = NetManager.make(url, paramsBuilder)

        val paramsString = NetManager.ParamBuilder().apply(paramsBuilder).build()

        @Suppress("NAME_SHADOWING")
        val url = if (paramsString.isEmpty()) url else "$url?$paramsString"
        console.log("$url: ")
        window.asDynamic().resp=resp
        console.log(resp)
        if(resp.error!= undefined){
            throw VkApiException(resp.error.error_code,resp.error.error_msg)
        }
        window.asDynamic().response=resp.response

        return resp.response
    }
}