package js

import kotlin.properties.ReadWriteProperty
import kotlin.reflect.KProperty


object DynamicField : ReadWriteProperty<dynamic, dynamic> {
    override fun getValue(thisRef: dynamic, property: KProperty<*>): dynamic {
        return thisRef[property.name]
    }

    override fun setValue(thisRef: dynamic, property: KProperty<*>, value: dynamic) {
        thisRef[property.name] = value
    }
}