package api.vk.methods

typealias UInt = Int
class Storage (val token: String) {
	suspend fun get(
		key: String = UNSET,
		keys: String = UNSET,
		user_id: UInt
	)= net.VkNetManager.make("https://api.vk.com/method/storage.get"){
		if(key.asDynamic() != UNSET) add("key",key)
		if(keys.asDynamic() != UNSET) add("keys",keys)
		if(user_id.asDynamic() != UNSET) add("user_id",user_id)
		add("access_token",token)
		add("v",5.131)
	}

	suspend fun getKeys(
		user_id: UInt = UNSET,
		offset: UInt = UNSET,
		count: UInt
	)= net.VkNetManager.make("https://api.vk.com/method/storage.getKeys"){
		if(user_id.asDynamic() != UNSET) add("user_id",user_id)
		if(offset.asDynamic() != UNSET) add("offset",offset)
		if(count.asDynamic() != UNSET) add("count",count)
		add("access_token",token)
		add("v",5.131)
	}

	suspend fun set(
		key: String,
		value: String = UNSET,
		user_id: UInt
	)= net.VkNetManager.make("https://api.vk.com/method/storage.set"){
		add("key",key)
		if(value.asDynamic() != UNSET) add("value",value)
		if(user_id.asDynamic() != UNSET) add("user_id",user_id)
		add("access_token",token)
		add("v",5.131)
	}

	companion object  {
		private val UNSET: dynamic = null
	}
}
