@file:Suppress("EnumEntryName")

package todo

import js.DynamicField
import kotlinx.browser.document
import org.w3c.dom.*
import org.w3c.dom.events.Event
import todo.EquipmentList.Equipment.Companion.date
import todo.EquipmentList.Equipment.Companion.millis
import todo.EquipmentList.Equipment.Companion.name
import todo.EquipmentList.Equipment.Companion.owner
import todo.EquipmentList.Equipment.Companion.state
import kotlin.js.Date

interface EquipmentDatabase {
    suspend fun getAll(): MutableList<EquipmentList.Equipment>
    fun add(equipment: EquipmentList.Equipment): Int
    fun update(index: Int, equipmentUpdated: (EquipmentList.Equipment) -> Unit)
    fun remove(index: Int)

}

enum class ListState {
    waiting, passed, inDeleteQueue;

    val id = ordinal

    companion object {
        val values = ListState.values()
    }
}

external fun prompt(title: String, default: String): String
enum class ListAction(val targetId: Int) {


    setPassed(ListState.passed),
    setWaiting(ListState.waiting),
    delete(ListState.inDeleteQueue),
    changeOwner(-1) {
        override fun apply(it: EquipmentList.Equipment, elemItem: HTMLElement) {
            val newOwner = prompt("Новый владелец", it.owner)
//            console.log("newOwner: ",newOwner)
           it.owner=newOwner
//            console.log("newOwner: ",newOwner)
            elemItem.querySelector(".list__owner__task")!!.firstElementChild!!.textContent= newOwner
//            console.log("outOwner")
        }
    }
    ;

    open fun apply(it: EquipmentList.Equipment, elemItem: HTMLElement) {
        it.state = targetId
        elemItem.dataset[stateKey] = targetId.toString();
    }

    val id = ordinal
    val cssName = run {
        val name = name
        buildString {
            var prev = 0;
            for ((index, c) in name.withIndex()) {
                if (c.isUpperCase()) {
                    append(name, prev, index)
                    append('-')
                    append(c.lowercase())
                    prev = index + 1;
                }
            }
            append(name, prev, name.length)
        }
    }

    constructor(target: ListState?) : this(target?.id ?: -1)

    companion object {
        val values = values();
    }
}

enum class ListFilter {
    waiting, noWaiting, all, trash;

    companion object {
        val values = values();
    }
}

private const val actionKey = "listAction"
private const val filterKey = "listFilter"
private const val stateKey = "listState"
private const val actionKey_ = "list-action"
private const val stateKey_ = "list-state"
private const val dateKey_ = "date"

class EquipmentList(val database: EquipmentDatabase, val scope: (suspend () -> Unit) -> Unit) {
    init {
        scope {
            val all = database.getAll()
            for ((i, equipment) in all.withIndex()) {
                addElement(i, equipment)
            }
        }
//        console.log("EVENTS")
        document.querySelector(".list__filters")!!
            .addEventListener("change", { update(it) });
//        window.onclick = { click(it) }
        document.addEventListener("click", { click(it) })
    }

    fun click(e: Event) {
//        console.log("Click: ")
//        console.log(e)
        val target = e.target!! as HTMLElement;
        if (target.classList.contains("list__action")) {
            val action = ListAction.values[target.dataset[actionKey]!!.toInt()];
            val elemItem = target.closest(".list__item")!! as HTMLElement;
            val index = elemItem.classList.asList().find { it.startsWith("idx") }!!.substring(3).toInt()
            val state = elemItem.dataset[stateKey]?.let { ListState.values[it.toInt()] }
//            console.log("elemItem.dataset[stateKey]: ", state)
            if (action === ListAction.delete && state === ListState.inDeleteQueue) {
                database.remove(index)
                elemItem.remove();
            } else {
                val date = Date()
                database.update(index) {
                    action.
                    apply(it, elemItem)
                    it.date = date.getTime()
                    elemItem.querySelector(".list__date")!!.textContent = "Изменено: ${date.toLocaleString("ru")}"
                }


            }
        } else if (target.classList.contains("list__add")) {
            val elemText = document.querySelector(".list__text") as HTMLInputElement;
            val elemOwner = document.querySelector(".list__owner") as HTMLInputElement;
            if (elemText.disabled || elemText.value.isEmpty()) {
                return;
            }
            this.addAndSave(Equipment(elemText.value, elemOwner.value, Date().getTime(), ListState.waiting));
            elemText.value = "";
        }
    }

    fun update(e: Event) {
        console.log("update: ", e)
        val option = ListFilter.values[(document.querySelector(".list__filters") as HTMLSelectElement).value.toInt()];
        (document.querySelector(".list__items")!! as HTMLElement).dataset[filterKey] = option.ordinal.toString();
        (document.querySelector(".list__text") as HTMLInputElement);
    }

    private fun addAndSave(equipment: Equipment) {
        val index = database.add(equipment)
        addElement(index, equipment)
    }

    private fun addElement(i: Int, equipment: Equipment) {
        val rawDate = Date(equipment.millis()).toLocaleString("ru")
        val date = JSON.stringify(Any().also {
            it.asDynamic().add = rawDate
        });
        val str = """<li class="list__item idx$i" data-${stateKey_}="${equipment.state}">
      <span class="list__task">
        ${equipment.name}
      </span>
      <p class="list__owner__task">
        <b>${equipment.owner}</b>
      </p>
      <span class="list__date" data-${dateKey_}="${rawDate}">
        <span>Изменено: ${rawDate}</span>
      </span>
      ${
            ListAction.values.joinToString("\n") { "<span class=\"list__action list__action_${it.cssName}\" data-${actionKey_}=\"${it.id}\"></span>" }
        }
      </li>"""
        document.querySelector(".list__items")!!
            .insertAdjacentHTML("beforeend", str);
    }

    interface Equipment {

        companion object {
            operator fun invoke(name: String, owner: String, date: Double, state: ListState): Equipment {
                val result: Equipment = Any().asDynamic()
                result.name = name;
                result.owner = owner;
                result.date = date;
                result.state = state.id;
                return result
            }

            fun cast(any: dynamic): Equipment {
                if (any.name == undefined) throw ClassCastException("Cannot cast $any as Equipment")
                if (any.owner == undefined) throw ClassCastException("Cannot cast $any as Equipment")
                if (any.date == undefined) throw ClassCastException("Cannot cast $any as Equipment")
                if (any.state == undefined) throw ClassCastException("Cannot cast $any as Equipment")
                return any
            }

            fun Equipment.millis() = date
            var Equipment.name: String by DynamicField
            var Equipment.owner: String by DynamicField

            /**
             * in millis/100
             * */
            var Equipment.date: Double by DynamicField
            var Equipment.state: Int by DynamicField
        }
    }
}
