package todo

import api.vk.methods.Storage
import getKey
import js.DynamicField
import kotlinx.browser.window
import kotlinx.coroutines.*

private external fun alert(text: String)
class VkDatabase(val storage: Storage, val scope: CoroutineScope) : EquipmentDatabase {

    val firstElementKey = "LIST"
    private var updateId = -1
    val userId: Int = 100;
    val regionLimit = 10;
    private val regions: MutableList<VkRegions> by DynamicField
    suspend fun regions(): List<VkRegions> {
        val self = this.asDynamic()
        if (self.regions !== undefined) return regions;
        window.asDynamic().toInt = { it: String -> it.toInt() }
        checkUpdateId()
        val regions = storage.getKey(firstElementKey).toIntOrNull() ?: 0
        console.log("regions: $regions")
        val vkRegions = mutableListOf<VkRegions>()
        if (regions > 0) {
            val intRange = 0 until regions
            val res = if (regions == 1) {
                storage.get(key = firstElementKey + "0", user_id = userId)
            } else {
                storage.get(keys = intRange.joinToString(",") { firstElementKey + it }, user_id = userId)
            }

            for (i in intRange) {
                val s = res[i].value as String
                val element = if (s.isEmpty()) {
                    VkRegions(i, mutableListOf())
                } else {
                    VkRegions(
                        i,
                        JSON.parse<Array<EquipmentList.Equipment>>(s).toMutableList()
                    )
                }
                vkRegions.add(element)
            }
        }
        self.regions = vkRegions
        return regions()
    }

    private fun checkUpdateId() {
        scope.launch {
            if (updateId == -1) {
                updateId = storage.getKey(firstElementKey + "_").toIntOrNull() ?: run {
                    storage.set(firstElementKey + "_", "0", userId)
                    0
                }
            } else {
                val serverUpdateId =
                    storage.getKey(firstElementKey + "_").toIntOrNull() ?: storage.getKey(firstElementKey + "_").toInt()
                if (serverUpdateId != updateId) {
                    alert("Ваша версия списка устарела. Требуеться перезагрузка страницы.")
                    window.location.reload()
                }
                console.log("updateId", serverUpdateId, updateId, serverUpdateId == updateId)
            }
        }
    }

    private fun increaseUpdateId() {
        scope.launch {
            updateId += 1
            storage.set(firstElementKey + "_", "" + updateId, userId)
            Unit
        }
    }

    private var all: MutableList<EquipmentList.Equipment> by DynamicField
    override suspend fun getAll(): MutableList<EquipmentList.Equipment> {
        if (asDynamic().all === undefined) {
            val flatMap = regions().flatMap { it.value }
            all = flatMap.toMutableList()
        }
        return all
    }

    override fun add(equipment: EquipmentList.Equipment): Int {
        checkUpdateId()
        val index = all.size
        all.add(equipment)
        var saveIndex = false;
        val element = if (regions.any() && regions.last().expandable()) {
            val last = regions.last()
            last.value.add(equipment)
            saveIndex = true;
            last
        } else {
            val element = VkRegions(regions.size, mutableListOf(equipment))
            regions.add(element)
            element
        }
        scope.launch {
            element.save()
            storage.set(key = firstElementKey, value = regions.size.toString(), user_id = userId)
            increaseUpdateId()
            Unit
        }
        return index
    }

    fun regionIndex(itemIndex: Int): Pair<Int, Int> {
        var i = itemIndex
        var regionIndex = 0;
        while (i >= regions[regionIndex].value.size) {
            i -= regions[regionIndex].value.size
            regionIndex++;
        }
        return regionIndex to i
    }

    override fun update(index: Int, equipmentUpdated: (EquipmentList.Equipment) -> Unit) {
        checkUpdateId()
        val (regionIndex, itemIndex) = regionIndex(index)
        equipmentUpdated(regions[regionIndex].value[itemIndex])
//        console.log("outUpdate")
        scope.launch {
//            console.log("beforeSave")
            regions[regionIndex].save()
            increaseUpdateId()
//            console.log("afterSave")
        }
    }

    override fun remove(index: Int) {
        checkUpdateId()
        val (regionIndex, itemIndex) = regionIndex(index)
        regions[regionIndex].value.removeAt(itemIndex)
        scope.launch {
            regions[regionIndex].save()
            increaseUpdateId()
        }
    }

    inner class VkRegions(val key: Int, val value: MutableList<EquipmentList.Equipment>) {
        suspend fun save() {
            console.log("$key -> " + storage.set(firstElementKey + key, data(), userId))
        }

        fun expandable(): Boolean {
            val bytes = data().encodeToByteArray()
            /*val perOne = bytes / value.size*/
            val limit = (1 shl 12);
            val myLimit = (1 shl 11) or (1 shl 10)
            return bytes.size <= myLimit
        }

        private fun data() = JSON.stringify(value.toTypedArray())

        var job: Job? = null

        constructor(key: Int) : this(key, mutableListOf()) {
            job = scope.launch {
                storage.getKey(key.toString())
                    .let { JSON.parse<Array<EquipmentList.Equipment>>(it) }
                    .let { value.addAll(it) }
            }
        }
    }
}