package net

import kotlinx.coroutines.await
import org.w3c.xhr.XMLHttpRequest
import kotlin.js.Promise

external fun encodeURIComponent(str: String): String

external fun make_request(url: String): Promise<Any>

object NetManager {
    suspend fun make(url: String, params: ParamBuilder.() -> Unit): dynamic {
        val paramsString = ParamBuilder().apply(params).build()

        @Suppress("NAME_SHADOWING")
        val url = if (paramsString.isEmpty()) url else "$url?$paramsString"
        return make_request(url).await()

    }

    class ParamBuilder {
        private val list = mutableListOf<Pair<String, String>>()


        fun add(name: String, value: Number) {
            list.add(name to value.toString())
        }

        fun add(name: String, value: Boolean) {
            list.add(name to (if (value) "1" else "0"))
        }

        fun add(name: String, value: String) {
            list.add(name to encodeURIComponent(value))
        }

        fun add(name: String, value: UByte) {
            list.add(name to value.toString())
        }

        fun add(name: String, value: UShort) {
            list.add(name to value.toString())
        }

        fun add(name: String, value: UInt) {
            list.add(name to value.toString())
        }

        fun add(name: String, value: ULong) {
            list.add(name to value.toString())
        }
        /* fun add(name: String, value: String) {
             list.add(name to encodeURIComponent(JSON.stringify(value)))
         }*/

        fun build(): String {
            return list.joinToString("&") { it.first + "=" + it.second }
        }
    }
}